html {
  font-size: 16px; }

body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: Helvetica, Arial, sans-serif;
  line-height: 1.3; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

ul li {
  margin: 0;
  padding: 0; }

ol {
  margin: 0;
  padding: 0; }

p {
  padding: 0;
  margin: 0px 0px 5px 0px;
  margin: 0rem 0rem 0.3125rem 0rem; }

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0px 0px 10px 0px;
  margin: 0rem 0rem 0.625rem 0rem;
  font-weight: normal; }

fieldset {
  border: none;
  margin: 0;
  padding: 0; }

button {
  cursor: pointer; }

.menu {
  position: relative;
  z-index: 3;
  background-color: pink;
  top: 0; }

.menu--header {
  z-index: 3;
  position: relative;
  background-color: pink; }

.menuList {
  position: absolute;
  z-index: 2;
  top: -35px;
  width: 100%; }

.menuList--item {
  background-color: grey; }
